<template>
  <div>
    <div class="status text-center">
      {{status}}
    </div>
    <ol>
      <li v-for="item in list">
        <div class="info">
          <div class="icon-box">
            <img src="@/assets/icon.png" width="38" height="38" alt="">
          </div>
          <span class="name">{{item.name}}</span>
        </div>
        <span class="extra">
          <router-link :to="{name:'Detail',params:{Id:item.id}}">
            <Button type="default" class="scan-btn">查看</Button>
          </router-link>
        </span>
      </li>
    </ol>
    <Nav></Nav>
  </div>
</template>
<script>
  import axios from 'axios'
  import Nav from '@/components/Nav.vue'
  export default {
    components:{
      Nav
    },
    data () {
      return {
        switchValue: true,
        list:[],
        searchParams:'',
        status:''
      }
    },
    mounted(){
      if(!localStorage.getItem('list')){
        axios.get('/inkSupSysSize',{
          params: {
            brand:this.$route.params.search,
            name:this.$route.params.search,
            size:this.$route.params.search
          }
        }).then( res => {
          if(res.data.data.content.length === 0){
            this.status='暂无结果'
          }
          this.list = res.data.data.content;
          localStorage.setItem('list',JSON.stringify(this.list));
        })
      }
      else{
        this.list=JSON.parse(localStorage.getItem('list'));
      }
    }
  }
</script>

<style lang="scss" scoped>
  ol{
    padding: 40px 20px;
  }
  li{
    line-height: 32px;
    height: 32px;
    font-size: 14px;
    margin-bottom: 10px;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .extra{
    float: right;
  }
  .text-center{
    text-align: center;
  }
  .icon-box{
    float: left;
    width: 59px;
    height: 59px;
    line-height: 59px;
    text-align: center;
    background: #f3f9ff;
    border-radius: 100%;
    margin-right: 14px;
    img{
      vertical-align: middle;
    }
  }
  .name{
    font-size: 18px;
    display: block;
    overflow: hidden;
  }
  .scan-btn{
    border-color: #979797;
    color: #37ac9b;
    width: 76px;
    height: 32px;
    border-radius: 4px;
    font-size: 18px;
    padding: 0;
    float: right;
  }
  .info{
    display: flex;
    flex-direction: row;
    align-items: center;
  }
</style>



