<template>
  <div class="layout">
    <Layout>
      <Content class="content">
        <div class="logo" @click="$router.push('/')">
          <img src="@/assets/logo_jf.png" alt="" height="45">
        </div>
        <h1>
          供墨系统
        </h1>
        <div class="seletor">
          <Form label-position="top">
            <FormItem>
              <h2>印刷名称</h2>
              <input type="text" class="searchInput" v-model="searchInput" placeholder="请输入名称">
            </FormItem>
            <FormItem style="text-align: center;">
              <router-link :to="searchInput&&{name:'List',params:{search:searchInput}}">
                <Button type="primary" size="large" @click="deleteLocalStorage" class="search-btn">查询</Button>
              </router-link>
              <!--<Button style="margin-left: 8px" size="large" @click="$router.push('/')">返回</Button>-->
            </FormItem>
          </Form>
        </div>
      </Content>
      <Footer class="footer">
        &#169 2018 版权所有 飓风网
      </Footer>
    </Layout>
  </div>
</template>

<script>
  export default {
    name: 'Index',
    data() {
      return {
        searchInput: ''
      }
    },
    methods: {
      deleteLocalStorage: function () {
        if (this.searchInput === '') {
          this.$Message.error('请输入有效值！');
        }
        localStorage.removeItem('list');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .seletor {
    padding: 50px 40px 24px;
  }

  h2 {
    margin-bottom: 9px;
    font-size: 15px;
    color: #ffffff;
    font-weight: normal;
  }

  .searchInput {
    border: #ffffff;
    height: 44px;
    border-radius: 5px;
    padding: 0 13px;
    font-size: 15px;
    display: block;
    width: 100%;
  }

  .search-btn {
    border-radius: 133px;
    height: 44px;
    background: #ffffff;
    font-size: 16px;
    color: #00a27f;
    text-align: center;
    border-color: #fff;
    width: 100%;
    display: block;
    margin-top: 16px;
  }

  .layout {
    background: linear-gradient(180deg, #3BCABD, #59EAA0);
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
  }

  .ivu-layout {
    background: none;
  }

  h1 {
    text-align: center;
    font-size: 29px;
    color: #ffffff;
    font-weight: normal;
  }

  .footer {
    text-align: center;
    position: fixed;
    left: 0;
    bottom: 0;
    background: none;
    color: #fff;
    font-size: 14px;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 25px;
  }

  .logo {
    text-align: center;
    margin: 129px 0 20px;
  }
</style>



