<template>
  <div class="detail">
    <h3>{{detail.name}}</h3>
    <div class="content" v-html="detail.size">
      {{detail.size}}
    </div>
    <!--<div class="text-center">-->
        <!--<Button type="default" @click="$router.back(-1)" >返回</Button>-->
    <!--</div>-->
    <Nav></Nav>
  </div>
</template>

<script>
  import axios from 'axios'
  import Nav from '@/components/Nav.vue'
  export default {
    name: 'Index',
    components:{
      Nav
    },
    data() {
      return {
        detail:''
      }
    },
    mounted(){
      axios.get('/inkSupSysSize/'+this.$route.params.Id).then( res => {
        this.detail = res.data.data;
      })
    }
  }
</script>

<style lang="scss" scoped>
  .detail{
    padding-bottom: 52px;
    padding-top: 30px;

  }
  h3 {
    margin-bottom: 15px;
    font-size: 16px;
    text-align: center;
  }
  .content{
    font-size: 14px;
    margin-bottom: 20px;
    padding: 0 24px;
  }
  .text-center{
    text-align: center;
  }
</style>



